@import 'mixin';


.section__model{
  @include display-block;
  max-width: 1000px;
  margin: 0 auto;

  &.main__margin{
    margin-top: 40px;
  }

  @media(max-width:1200px){
    max-width: 992px;
  }
  @media(max-width:992px){
    max-width: inherit;
    width: calc(100% - 40px);
  }
  @media(max-width:768px){
    width: calc(100% - 30px);
  }
}


.main__panel{
  @include flex-row;
  @include align-start;
  .canvas__panel{
    @include display-block;
    width: 85%;
    background: #222227;
    canvas{
      @include display-block;
      height: 100%;
    }
  }
  .canvas__btns{
    @include display-block;
    width: 15%;
      button{
        @include display-block;
        width: 80%;
        aspect-ratio: 1/1;
        margin: 12% auto;
        background: #49537B;
        background-image: linear-gradient( 90deg , #49537B 0% , #393734 100%);
        border-radius: 10%;
        border: solid 2px #5E7788;
        font: 700 50px/50px 'Inter', sans-serif;
        color: #fff;
        &:hover{
          background-image: linear-gradient( 27deg , #49537B 0% , #393734 100%);
          transform: translateY(2px);
        }
      }
  }
  @media(max-width:992px){
    .canvas__btns button{
      font: 700 40px/40px 'Inter', sans-serif;
    }
  }
  @media(max-width:768px){
    flex-wrap: wrap;
    .canvas__panel{
      width: 100%;
    }
    .canvas__btns{
      @include flex-row;
      @include justify-space-between;
      margin-top: 30px;
      button{
        width: 15%;
        margin: 0;
        font: 700 35px/35px 'Inter', sans-serif;
      }
    }
  }
  @media(max-width:576px){
    .canvas__btns button{
      font: 600 30px/30px 'Inter', sans-serif;
    }
  }
}
